<template>
  <v-content>
    <v-container fluid="fluid" fill-height="fill-height">
      <v-layout align-center="align-center" justify-center="justify-center">
        <v-flex xs12="xs12" sm8="sm8" md4="md4">
          <v-card>
            <v-toolbar class="elevation-0" dark="dark" color="red darken-2">
              <v-toolbar-title>Iniciar sesión</v-toolbar-title>
            </v-toolbar>
            <v-form ref="login_form" lazy-validation v-model="isValid">
              <v-container>
                <v-layout wrap row class="login">
                  <v-flex>
                    <v-text-field
                      v-model="user.info"
                      :rules="user.rules"
                      label="Correo"
                      prepend-icon="person"
                      clearable="clearable"
                      autocomplete="email"
                      type="email"
                    ></v-text-field>
                  </v-flex>
                  <v-flex class="flex_sesion">
                    <v-text-field
                      v-model="password.info"
                      :rules="password.rules"
                      label="Contraseña"
                      prepend-icon="lock"
                      clearable="clearable"
                      autocomplete="autocomplete"
                      type="password"
                    ></v-text-field>
                  </v-flex>
                </v-layout>
              </v-container>
            </v-form>
            <v-layout>
              <!-- <v-flex>
                <p class="text-lg-right pr-4 botones">
                  <a class="ctr">¿Olvidaste tu contraseña?</a>
                </p>
              </v-flex>-->
            </v-layout>
            <v-layout>
              <v-flex>
                <p class="text-md-center">
                  <v-btn
                    class="elevation-0"
                    id="enviar"
                    dark="dark"
                    color="red"
                    @click="submit"
                  >Entrar</v-btn>
                </p>
              </v-flex>
            </v-layout>
            <v-layout>
              <v-flex>
                <p class="text-lg-center NUsuario">
                  ¿Nuevo usuario?.
                  <em></em>
                  <router-link to="/registro">
                    <a class="ctr">Regístrate aquí</a>
                  </router-link>
                </p>
              </v-flex>
            </v-layout>
          </v-card>
        </v-flex>
      </v-layout>
    </v-container>
  </v-content>
</template>

<script>
import LoginRequest from "./LoginRequest";
import { mapActions } from "vuex";

export default {
  name: "LoginPage",
  data() {
    return {
      isValid: true,
      user: {
        info: "",
        rules: [
          v => !!v || "El correo es requerido",
          v => /.+@.+/.test(v) || "Ingresa un correo válido"
        ]
      },
      password: {
        info: "",
        rules: [v => !!v || "La contraseña es requerida"]
      }
    };
  },
  methods: {
    ...mapActions({
      login: "login"
    }),
    enter() {
      console.log("Se apretó el enter");
    },
    async submit() {
      if (this.$refs.login_form.validate() == false) {
        return;
      }
      let request = new LoginRequest(this.user.info, this.password.info);
      await this.login(request.toJSON());
      if (this.$store.state.jwt !== "") {
        this.$router.push("/");
      }
      this.user.info = "";
      this.password.info = "";
    }
  }
};
</script>
<style>
.login {
  padding: 10px;
}
.botones {
  font-size: 0.8em;
}
.v-icon {
  font-size: 20px !important;
  color: #000;
}
.ctr {
  color: rgba(0, 0, 0, 1);
  /* margin-left: 60%; */
}
.ctr:hover {
  text-decoration: underline;
}
.NUsuario {
  font-size: 1em;
}
</style>



